
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
ViewBox,
XHeader,
Masker,
Tab,
TabItem,
Group,
XInput,
XSwitch,
Cell
},
data () {
return {
index: 0,
loginByAccount: true,
phoneNum: '',
verifyCode: '',
verifyCode1: '',
pwd: '',
showClear: false,
time: 60,
showDialog: false
}
},
mounted () {

},
methods: {
getBaseInfo () {

let self = this
let data = {
action: 'get_request'
}

self.$store.dispatch('normalRequest', data).then(res => {

self.$store.commit('INIT_USER_INFO', res)
})
},
closeDialog () {
this.showDialog = false
},
backToMy () {
this.$router.go(-1)
},
clickTitle () {
},
handler (flag) {
if (flag === 0) {
this.loginByAccount = true
} else {
this.loginByAccount = false
}
},
toRegister () {
this.$router.push('/register')
},
sendTextCode (type, id) {

this.setBtnDisabled(id)
this.getCode(type)
},
login () {
let self = this
if (self.isPhoneCorrect(self.phoneNum)) {
if (self.loginByAccount) {
self.loginByAccountWays()
} else {
self.loginByVerifyCodeWays()
}
}
},
secondLogin () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/twoAuthenticationLogin.do'
let data = {
action: 'hand_request',
path: path,
params: {
cellphone: self.phoneNum,
password: self.pwd,
channel: self.$store.state.channel,
imei: self.$store.state.imei,
verifyCode: self.verifyCode1
}
}
if (self.mathchPwd(self.pwd)) {

this.$vux.loading.show({
text: 'Loading'
})
self.$store.dispatch('normalRequest', data).then(res => {

self.$store.commit('INIT_USER_INFO', res)

this.$vux.loading.hide()
this.phoneNum = ''
this.verifyCode = ''
this.pwd = ''
this.$vux.toast.text('')
self.$router.replace('/my')
})
}
this.$vux.loading.hide()
},
loginByAccountWays () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/login.do'
let data = {
action: 'hand_request',
path: path,
params: {
cellphone: self.phoneNum,
password: self.pwd,
channel: self.$store.state.channel,
imei: self.$store.state.imei
}
}
if (self.mathchPwd(self.pwd)) {

this.$vux.loading.show({
text: 'Loading'
})
self.$store.dispatch('normalRequest', data).then(res => {

if (res.device !== 'different') {
self.$store.commit('INIT_USER_INFO', res)

this.$vux.loading.hide()
this.phoneNum = ''
this.verifyCode = ''
this.pwd = ''
this.$vux.toast.text('')
self.$router.replace('/my')
} else {
this.showDialog = true
}
})
}
this.$vux.loading.hide()
},
loginByVerifyCodeWays () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/quickLogin.do'
let data = {
action: 'hand_request',
path: path,
params: {
cellphone: self.phoneNum,
verifyCode: self.verifyCode,
channel: self.$store.state.channel,
imei: self.$store.state.imei
}
}
if (self.verifyCode !== '') {

this.$vux.loading.show({
text: 'Loading'
})
self.$store.dispatch('normalRequest', data).then(res => {
self.$store.commit('INIT_USER_INFO', res)

this.$vux.loading.hide()
this.$vux.toast.text('')
this.phoneNum = ''
this.verifyCode = ''
this.pwd = ''
self.$router.replace('/my')
})
} else {
this.$vux.loading.hide()
this.$vux.toast.text('')
}
this.$vux.loading.hide()
},
forgetPwd () {
this.$router.push('forgotPwd')
},
getCode (type) {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/getVerifyCode.do'
let data = {
action: 'init_request',
path: path,
params: {
cellphone: self.phoneNum,
smsType: type, // 
channel: self.$store.state.channel
}
}
if (self.isPhoneCorrect(self.phoneNum)) {
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
self.$vux.toast.text('')
} else {
self.$vux.toast.text(res.data)
}
})
}
}
}
}



